.footer {
  background-color: #2e7d32; /* green theme */
  color: white;
  padding: 20px;
  text-align: center;
  margin-top: 40px;
}

.footer-links {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
}

.footer-links a {
  color: #ffeb3b; /* yellowish link */
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #ffffff;
  text-decoration: underline;
}

.footer-copy {
  font-size: 14px;
  opacity: 0.8;
}
