/* src/components/Navbar.css */
.navbar {
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px;
  background: #007bff;
  color: #fff;
  gap: 16px;
}

.logo-img {
  height: 70px;   /* bigger logo */
  width: auto;
  display: block;
}

.navbar-links {
  display: flex;
  gap: 18px;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
}

.navbar-links a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.navbar-links a:hover {
  text-decoration: underline;
}
