/* src/components/OrderModal.css */

/* Fullscreen overlay background */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4); /* subtle backdrop */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* The actual modal box */
.modal {
  background: #ffffff;
  padding: 25px;
  border-radius: 10px;
  width: 90%;
  max-width: 450px;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
  z-index: 1001;
  color: #333;
  /* ‚ùå removed ALL animations */
}

/* Title and description */
.modal h2 {
  margin-bottom: 15px;
  font-size: 20px;
  color: #2e7d32; /* green */
}

.modal p {
  font-size: 15px;
  margin-bottom: 20px;
  line-height: 1.4;
  color: #444;
}

/* Buttons */
.modal-buttons {
  display: flex;
  justify-content: space-between;
}

.modal-buttons button {
  padding: 10px 18px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s ease;
}

/* Cancel button */
.modal-buttons .cancel {
  background-color: #ccc;
  color: #333;
}

.modal-buttons .cancel:hover {
  background-color: #b0b0b0;
}

/* Proceed button */
.modal-buttons .proceed {
  background-color: #2e7d32;
  color: #fff;
}

.modal-buttons .proceed:hover {
  background-color: #1b5e20;
}
