/* ===== Navbar Styling ===== */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  flex-wrap: wrap;
}

.navbar-logo {
  font-size: 1.1rem;
  font-weight: bold;
  white-space: nowrap;
  margin-right: 20px;
}

.navbar-links {
  display: flex;
  gap: 18px;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
}

.navbar-links li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.navbar-links li a:hover {
  text-decoration: underline;
}

/* ===== Welcome Section ===== */
.welcome-section {
  text-align: center;
  padding: 60px 20px;
  background: #f9f9f9;
  color: #000;
}
.welcome-section h1 {
  font-size: 2rem;
  margin-bottom: 10px;
}
.welcome-section p {
  font-size: 1.1rem;
}

/* ===== Services Layout ===== */
.services-section {
  padding: 40px 20px;
}
.services-section h2 {
  text-align: center;
  margin-bottom: 20px;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.service-card {
  background: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s ease;
}
.service-card:hover {
  transform: translateY(-5px);
}
.service-card img {
  max-width: 100%;
  height: 150px;
  object-fit: contain;
  margin-bottom: 15px;
}
.service-card h3 {
  margin-bottom: 10px;
}
.service-card p {
  margin-bottom: 15px;
}
.service-card button {
  padding: 10px 15px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.service-card button:hover {
  background: #0056b3;
}

/* ===== Footer ===== */
.footer {
  background: #222;
  color: white;
  padding: 20px;
  text-align: center;
}
.footer a {
  color: #00ff88;
  text-decoration: none;
  font-weight: bold;
}
.footer a:hover {
  text-decoration: underline;
}
